import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  DataGridComponent,
  DataSourceModifier,
  FilteringActionType,
  FilteringModifier,
  gettext,
  Pagination,
  Row,
  ServerSideDataResult
} from '@c8y/ngx-components';
import { DeviceGridColumn } from '@c8y/ngx-components/device-grid';
import { IManagedObject, SmartGroupsService } from '@c8y/client';
import { AssetSearchService } from './search.service';
import {
  AssetTypeGridColumn,
  DeleteAssetsModalComponent,
  DeleteModalCheckboxes,
  SubAssetsService
} from '@c8y/ngx-components/sub-assets';
import { BsModalService } from 'ngx-bootstrap/modal';
import {
  AlarmsDeviceGridColumn,
  DeviceGridService,
  ImeiDeviceGridColumn,
  ModelDeviceGridColumn,
  NameDeviceGridColumn,
  RegistrationDateDeviceGridColumn,
  SerialNumberDeviceGridColumn,
  SystemIdDeviceGridColumn
} from '@c8y/ngx-components/device-grid';

@Component({
  selector: 'c8y-search-grid',
  templateUrl: './search-grid.component.html'
})
export class SearchGridComponent {
  @Input('parent-group') parentGroup: IManagedObject;
  @Input() title: string = '';
  @Input() loadingItemsLabel: string = gettext('Loading results…');
  @Input('columns') set _columns(value: DeviceGridColumn[]) {
    if (value) {
      this.columns = value;
    } else {
      this.columns = this.searchService.getDefaultColumns();
    }
  }
  @Input('pagination') set _pagination(value: Pagination) {
    if (value) {
      this.pagination = value;
    } else {
      this.pagination = this.searchService.getDefaultPagination();
    }
  }
  @Input('actionControls') set _actionControls(value: ActionControl[]) {
    if (value) {
      this.actionControls = value;
    } else {
      this.actionControls = this.searchService.getDefaultActionControls();
    }
  }
  @Input() selectable: boolean = false;
  @Input('bulkActionControls') set _bulkActionControls(value: BulkActionControl[]) {
    if (value) {
      this.bulkActionControls = value;
    } else {
      this.bulkActionControls = this.searchService.getDefaultBulkActionControls();
    }
  }
  @Output() onColumnsChange: EventEmitter<DeviceGridColumn[]> = new EventEmitter<
    DeviceGridColumn[]
  >();

  @Input()
  searchText = '';

  @Input()
  filteringName: string;

  columns: DeviceGridColumn[];
  pagination: Pagination = this.searchService.getDefaultPagination();
  actionControls: ActionControl[];
  bulkActionControls: BulkActionControl[] = this.searchService.getDefaultBulkActionControls();
  serverSideDataCallback: any;
  refresh: EventEmitter<any> = new EventEmitter();

  @ViewChild(DataGridComponent, { static: true })
  dataGrid: DataGridComponent;

  private sizeCount = 0;

  constructor(
    public searchService: AssetSearchService,
    private bsModalService: BsModalService,
    private smartGroupsService: SmartGroupsService,
    private subAssetsGridService: SubAssetsService
  ) {}

  ngOnInit(): void {
    if (!this.filteringName) {
      this.columns = this.searchService.getDefaultColumns();
    } else {
      this.columns = [
        new AssetTypeGridColumn({ sortOrder: 'desc' }),
        new NameDeviceGridColumn({
          sortOrder: 'asc',
          filter: { externalFilterQuery: { names: [this.filteringName] } }
        }),
        new ModelDeviceGridColumn(),
        new SerialNumberDeviceGridColumn({ visible: false }),
        new RegistrationDateDeviceGridColumn({ visible: false }),
        new SystemIdDeviceGridColumn({ visible: false }),
        new ImeiDeviceGridColumn({ visible: false }),
        new AlarmsDeviceGridColumn()
      ];
    }
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
    this.setActionControls();
  }

  trackByName(_index, column: DeviceGridColumn): string {
    return column.name;
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    let response;
    if (dataSourceModifier.searchText) {
      response = await this.searchService.search(
        dataSourceModifier.searchText,
        dataSourceModifier.pagination
      );
    } else {
      response = await this.searchService.getData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        undefined
      );
    }
    const { res, data, paging } = response;
    const filteredData = this.searchService.filterOnlyAssets(data);

    if (paging.currentPage === 1) {
      this.sizeCount = 0;
    }
    this.sizeCount += filteredData.length;
    this.onColumnsChange.emit(dataSourceModifier.columns);

    return {
      res,
      data: filteredData,
      paging,
      filteredSize: this.sizeCount,
      size: undefined
    };
  }

  setActionControls() {
    const actionControls: ActionControl[] = [];
    const deleteAction: ActionControl = {
      type: BuiltInActionType.Delete,
      callback: (asset: Row) => this.onDeleteAsset(asset as IManagedObject, this.parentGroup)
    };
    actionControls.push(deleteAction);
    if (!this.actionControls) {
      this.actionControls = actionControls;
    }
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    }
  ) {
    const { type } = action;
    if (type === FilteringActionType.ResetFilter) {
      this.dataGrid.clearFilters();
    } else {
      this.dataGrid.updateFiltering(columnNames, action, false);
    }
  }

  configChange(config) {
    this.searchService.saveConfig(config);
  }

  private onDeleteAsset(asset, parentRef) {
    const initialState = {
      showWithDeviceUserCheckbox: this.subAssetsGridService.shouldShowWithDeviceUserCheckbox(asset),
      asset,
      showWithCascadeCheckbox:
        !this.smartGroupsService.isSmartGroup(asset) &&
        !this.smartGroupsService.isSmartGroupV2(asset)
    };

    const modalRef = this.bsModalService.show(DeleteAssetsModalComponent, { initialState });

    modalRef.content.closeSubject.subscribe(async (result: DeleteModalCheckboxes) => {
      if (result) {
        await this.subAssetsGridService.deleteAsset(asset, parentRef, result);
        this.refresh.emit();
      }
    });
  }
}
